*
{
    margin: 0;
    padding: 0;
}

html,
body
{
    height: 100vh;
    font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    background-color: #121212;
}

.webgl
{
    position: absolute;
    top: 0;
    left: 0;
    outline: none;
    background: url('../static/textures/spaceBackground.jpg');
}

.container {
    height: 100vh;
display: grid;
place-items: center;

}

h1 {
    font-size: 8rem;
    text-transform: capitalize;
    color: #fff;
    text-align: center;
    z-index: 99;
    opacity: .5;
}

section {
    height: 100vh;
}